import React from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement } from "chart.js";

import style from './chart.module.scss'

ChartJS.register(ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement)

function createDatesArray() {
  var datesArray = [];
  var today = new Date();
  for (var i = 0; i < 5; i++) {
      var date = new Date(today);
      date.setDate(today.getDate() - i);
      var day = ('0' + date.getDate()).slice(-2);
      var month = ('0' + (date.getMonth() + 1)).slice(-2);
      var formattedDate = day + '.' + month;
      datesArray.push(formattedDate);
  }
  return datesArray.reverse();
}


function shiftArrayByDateDifference(arr, lastDate, todayDate) {
  let _arr = [...arr]
  //     Date
  const last = new Date(lastDate);
  const today = new Date(todayDate);
  
  //          
  const timeDiff = Math.abs(today.getTime() - last.getTime());
  const dayDiff = Math.ceil(timeDiff / (1000 * 3600 * 24)); //         
  console.log(dayDiff)
  //     ,   
  if (last > today) {
      for (let i = 0; i < dayDiff; i++) {
        _arr.unshift(0); //  0   
        _arr.pop(); //    
      }
  }
  //     ,   
  else if (last < today) {
      for (let i = 0; i < dayDiff; i++) {
        _arr.push(0); //  0   
        _arr.shift(); //    
      }
  }
  console.log(arr)
  return _arr;
}



const Chart = ({className, userData, lastWork}) => {
  //    (5 )
  // userData = userData.sort((a, b) => a - b)
  const data = {
    labels: createDatesArray(),
    datasets: [
      {
        label: ' ',
        data: lastWork.slice(0,10) != new Date().toJSON().slice(0,10) ? shiftArrayByDateDifference(userData, lastWork, new Date().toJSON().slice(0,10)) : userData || [0,0,0,0,0], //    
        backgroundColor: '#D5B9B2',
        borderColor: '#BFB5AF',
        borderWidth: 2,
        borderRadius: 1000,
        width: 445
      },
    ],
  };

  //    
  const options = {
    scales: {
        y: {
            grid: {
                display: false
            },
            ticks: {
                display: false,
            },
        },
        x: {
            grid: {
                display: false
            },
            barPercentage: 0.2
        },
        yAxes: [{
            ticks: {
                beginAtZero: true
            }
        }],
        xAxes: [{
            // Change here
            barPercentage: 0.2
        }]
    },
    plugins: {
        legend: {
          display: false,
        },           
    }
}

  return (
    <div className={className}>
      <div className={style.bar}>
        <Bar data={data} options={options} />
      </div>
    </div>
    
  );
};

export default Chart;
